@import "mixins";

:root {
  --scheme: dark;
  --grid-gap: 38px;
  --offset: 15px;
  --main-bg: #151515;
  --main-text: #fff;
  --second-text: #8d97a4;
  --accent: #bd082a;
  --accent-active: #8f0d24;
  --use-bg: #222;

  @include mobile-max {
    --grid-gap: 30px;
  }
}

.light-theme {
  --scheme: light;
  --main-bg: #fff;
  --main-text: #151515;
  --second-text: #8d97a4;
  --accent: #bd082a;
  --use-bg: #eff1f4;
}

@media (prefers-color-scheme: light) {
  :root {
    --scheme: light;
  }
}
