@mixin desktop {
  @media (min-width: (1025px)) {
    @content;
  }
}

@mixin desktop-max {
  @media (max-width: (1440px)) {
    @content;
  }
}

@mixin tablet-max {
  @media (max-width: (1024px)) {
    @content;
  }
}

@mixin tablet-min {
  @media (max-width: (768px)) {
    @content;
  }
}

@mixin mobile-max {
  @media (max-width: (576px)) {
    @content;
  }
}

@mixin mobile {
  @media (max-width: (375px)) {
    @content;
  }
}

@mixin desktop-height {
  @media (max-height: (749.9px)) and (min-width: (1025px)) {
    @content;
  }
}

@mixin desktop-height-min {
  @media (max-height: (639.9px)) and (min-width: (1025px)) {
    @content;
  }
}
