html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  font-family: "NotoSans", sans-serif;
  font-weight: 400;
  background-color: var(--main-bg);
}

img {
  max-width: 100%;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

.is-outer {
  position: relative !important; // stylelint-disable-line declaration-no-important
  left: -9999px !important; // stylelint-disable-line declaration-no-important
}

.site-container {
  overflow: hidden;
}

.btn-reset {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
}

.container {
  margin: 0 auto;
  padding: 0 var(--offset);
  max-width: 1410px;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.is-hidden {
  display: none !important; // stylelint-disable-line declaration-no-important
}

.title {
  margin: 0;
  font-weight: 400;
  font-size: 30px;
  line-height: 41px;
  color: var(--main-text);

  @include mobile-max {
    margin-bottom: 20px;
  }
}

.link {
  position: relative;
  display: inline-block;
  font-weight: 400;
  font-size: 20px;
  line-height: 27px;
  color: var(--accent);

  &::after {
    content: "";
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 1px;
    background-color: currentColor;
    transform: scaleX(0);
    transition: transform 0.3s ease-in-out;
  }

  @include desktop {
    &:hover {
      &::after {
        transform: scaleX(1);
      }
    }
  }

  &:focus {
    outline: 1px solid var(--accent);
    color: var(--accent);
  }
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.work-page .footer__content {
  border-top: none;
}

.consult-page .footer__content {
  border-top: none;
}

.grid {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  gap: var(--grid-gap);

  @include tablet-max {
    grid-template-columns: repeat(8, 1fr);
  }

  @include mobile-max {
    grid-template-columns: repeat(4, 1fr);
  }
}

.disable-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
}
